.label {
  margin-top: 15px;
}
.labelWrap {
  display: inline-block;
}
.labelWrap > * {
  display: inline-block;
}
.labelWrap .labelTitle {
  width: 80px;
  text-align: right;
  float: left;
  margin-top: 5px;
}
.labelWrap .labelCont {
  width: 200px;
  padding: 0 8px;
}
.labelWrap:after {
  content: " ";
  clear: both;
  display: block;
}
.labelSelect {
  width: 100%;
}
